@import "../global/variables";

.side-nav {
    position: fixed;
    background-color:#f5f5f5;
    height: 100vh;
    padding-top: 60px;
    padding-bottom: 4rem;
    .nav {
        width: 250px;
        margin-left: 1rem;
        margin-top: 10px;
    }

    overflow-y: auto;

    /* width */
    ::-webkit-scrollbar {
        width: 6px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
        background: #888;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

}

.menu {
    position: sticky;
    bottom: 0;
    left: 0;
    background-color: whitesmoke;

}

@media only screen and (max-width: 600px) {
    .side-nav {
        width:0;
    }
  }